import Vue from 'vue'
import VueRouter from 'vue-router'

const Two = () => import('views/two/Two.vue')
const Sudoku = () => import('views/sudoku/Sudoku.vue')
const Fantu = () => import('views/fantu/Fantu.vue')
const Qrcode = () => import('views/qrcode/Qrcode.vue')
const Three = () => import('views/three/Three.vue')
const Rubik = () => import('views/rubik/rubik.vue')
const Rubikcube = () => import('views/rubikcube/rubikcube.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/two',
   // name: 'home',
    component: Two
  },
  {
    path:'/sudoku',
    component:Sudoku
  },
  {
    path:'/fantu',
    component:Fantu
  },
  {
    path:'/qrcode',
    component:Qrcode
  },
  {
    path:'/three',
    component:Three
  },
  {
    path:'/rubik',
    component:Rubik
  },
  {
    path:'/rubikcube',
    component:Rubikcube
  }
]

const router = new VueRouter({
  //mode: 'history',//打包之后有bug//严重，404
  base: process.env.BASE_URL,
  routes
})

export default router
